header {
    min-height: 60px;
    background: $grey;
    display: flex; }
.logo-container {
    width: 35%;
    @include flex(center, center); }
.menu-container {
    width: 30%;

    @include flex(center, around); }
.search-container {
    width: 15%;
    @include flex(center, around); }
.user-container {
    width: 20%;
    @include flex(center, around); }
.logo {
    @include link(white);
    width: 96px;
    height: 29px; }
.menu-button {
    width: 33%;
    height: 100%;
    @include flex(center, flex-start); }
header a {
    @include link($light-grey);
    @include text-settings(16px, $light-grey);
    padding-left: 10px; }
header a:hover {
    @include link(white); }
.arrow {
    @include bg;
    width: 12px;
    height: 7px;
    margin-left: 8px;
    background-image: url(./images/Shape.png); }
.search {
    width: 100%;
    height: 38px;
    border: 1px solid #CCCCCC;
    border-radius: 4px; }
.search input {
    border: none;
    width: 100%;
    height: 100%;
    padding-left: 10px; }
.search input::placeholder {
    padding-left: 15px;
    @include text-settings(16px, #435766); }
.user-icon {
    @include bg;
    width: 20px;
    height: 20px;
    margin-left: 29px;
    margin-right: 5px;
    background-image: url(./images/user-icon.png); }
.drop-menu {
    visibility: hidden;
    position: absolute;
    top: 60px;
    width: 235px;
    z-index: 1;
    background: $grey;
    display: flex;
    flex-direction: column; }
.menu-button:hover .drop-menu {
    visibility: visible;
    -webkit-transition: all 1000ms;
    -moz-transition: all 1000ms;
    -o-transition: all 1000ms;
    transition: all 1000ms; }
.drop-menu a {
    padding: 10px; }
.drop-menu a:hover {
    padding-left: 20px;
    -webkit-transition: all 500ms;
    -moz-transition: all 500ms;
    -o-transition: all 500ms;
    transition: all 500ms; }
